.formulario{
	p{
		font-size: 1.4rem;
		color: $grisOscuro;
		margin: 2rem 0 0 0;
	}

	fieldset{
		margin-top: 2rem;
	}

	legend{
		font-size: 2rem;
		color: $grisOscuro;
	}

	label{
		font-weight: $bold;
		display: block;
		text-transform: uppercase;
	}

	input:not([type="submit"]),
	textarea,
	select
	{
		padding: 1rem;
		display: block;
		width: 100%;
		background-color: $blanco;
		border: 1px solid $gris;
		outline: none;
		transition: border 300ms;
		border-radius: .5rem ;

		&:focus{
			border: 1px solid $amarillo;
		}
	}

	textarea {
		height: 20rem;
	}

	.forma-contacto{
		max-width: 30rem;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	input[type="radio"]{
		margin: 0;
		width: auto;
	}
}